.header {
  background: rgba(255, 255, 255, 0.6);
  // .header__top
  &__top {
    padding: 15px 0;

    &-burger{
      display: none;
    }
    &-container{
      justify-content: space-between;
    }
  }

  // .header__logo

  &__logo {
  }

  // .header__phone

  &__phone {
  }

  // .header__bottom

  &__bottom {
    border-top: 1px solid #F5F5F5;
  }

  // .header__navigation

  &__navigation {
    // .header__navigation-container
  
    &-container {
      flex-wrap: wrap;

      list-style: none;
    }
  
    // .header__navigation-item
  
    &-item {
      box-sizing: content-box;
      margin: 15px 140px 15px 0;
      &:last-child{
        margin-right: 0;
      }
    }
  
    // .header__navigation-link
  
    &-link {
      color: $text-color;
      text-decoration: none;
    }
  
    // .header__navigation-btn
  
    &-btn {
      display: none;
    }
  }

}

// <1280
@include media-laptop {}
// <992
@include media-tablet-horizontal {
  .header{
    &__navigation{
      &-item{
        margin-right: 60px;
      }
    }
  }
}
// <768
@include media-tablet {
  .header{
    &__logo{
      flex: 1 1 33.33%;
    }
    &__phone{
      flex: 1 1 33.33%;
    }
    &__top{
      position: fixed;
      top: 0;
      left: 0;
      z-index: 3;

      width: 100%;


      padding: 5px 0;

      background: rgb(255, 255, 255);
      &-burger{
        position: relative;

        display: block;
        width: 30px;
        height: 20px;
        span{
          position: absolute;
          left: 0;

          width: 100%;
          height: 2px;

          background-color: #222;

          transition: all .3s ease-in-out;
          &:nth-child(1){
            top: 0;
          }
          &:nth-child(2){
            top: 50%;

            transform: translateY(-50%);
          }
          &:nth-child(3){
            bottom: 0;
          }
        }
        &.menu-open{
          span{
            &:nth-child(1){
              top: 50%;

              transform: rotate(45deg) translateY(-50%);
            }
            &:nth-child(2){
              transform: rotate(45deg) translateY(-50%);
            }
            &:nth-child(3){
              bottom: 50%;

              transform: rotate(-45deg) translateY(50%);
            }
          }
        }
      }
    }
    &__bottom{
      position: fixed;
      top: -100%;
      left: 0;
      z-index: 2;

      width: 100%;
      height: 100%;
      padding-top: 58px;
      overflow: auto;

      background: #FFF;

      transition: all .5s ease;
      &.menu-open{
        top: 0%;
        
      }
    }
    &__navigation{
      &-container{
        flex-direction: column;
      }
      &-item{
        margin-right: 0;
      }
    }
  }
}
