.footer {
  padding: 30px 0 16px;

  background: #2E4357;
  backdrop-filter: blur(70px);
  // .footer__top
  &__top {
    justify-content: space-between;
  }

  // .footer__logo
  &__logo {
    flex-shrink: 0;
  }
  
  // .footer__navigation
  &__navigation {
    flex: 0 1 440px;
    margin-top: 8px;
    margin-left: 19px;
    &-list {
      display: flex;
      flex: 1 1 auto;
      flex-wrap: wrap;
      align-items: center;

      list-style: none;
    }
  
    // .footer__navigation-item
    &-item {
      flex: 1 1 50%;
      margin-bottom: 5px;
      &:last-child{
        margin-bottom: 0; 
      }
      &:nth-of-type(1),&:nth-of-type(3){
        flex-basis: 30%;
      }
    }
    
    // .footer__navigation-link
    &-link {
      color: #FFF;
      font-weight: 300;  
      text-decoration: none;
    }
  }

  // .footer__whatsapp
  &__whatsapp {
    flex-shrink: 0;
    .whatsapp__text{
      margin-top: 5px;

      font-weight: 400;
      font-size: 16px;
    }
  }
  
  // .footer__bottom
  &__bottom {
    margin-top: 18px;
  }

  // .footer__copyright
  &__copyright {
    color: rgba(255, 255, 255, 0.6);
    font-weight: 350;
  }
}


// <1280
@include media-laptop {}
// <992
@include media-tablet-horizontal {}
// <768
@include media-tablet {
  .footer{
    &__top{
      flex-wrap: wrap;
      align-items: center;
    }
    &__navigation{
      flex: 1 1 100%;
      order: 1;
      margin: 20px 0 0;
      &-list{
        flex-direction: column;
        align-items: flex-start;
      }
    }
  }
}
