$fz-subtitle: 32px;
$fz-small-title: 24px;
$fz-big: 20px;
$fz-average: 18px;
$fz-normal: 16px;
$fz-small-normal: 15px;
$fz-small: 14px;
$fz-tiny: 12px;
$fz-bittwo: 28px;
$line-height: 1.6;
.fz-bigger {
  // 54
  font-size: $fz-bigger;
}

.fz-title {
  // 40
  font-size: $fz-title;
}

.fz-average-title {
  // 38
  font-size: $fz-average-title;
}

.fz-subtitle {
  // 32
  font-size: $fz-subtitle;
}

.fz-small-title {
  // 24
  font-size: $fz-small-title;
}

.fz-big {
  // 20
  font-size: $fz-big;
}

.fz-average {
  // 18
  font-size: $fz-average;
}

.fz-normal {
  // 16
  font-size: $fz-normal;
}

.fz-small-normal {
  // 15
  font-size: $fz-small-normal;
}

.fz-small {
  // 14
  font-size: $fz-small;
}

.fz-tiny {
  // 12
  font-size: $fz-tiny;
}

p,
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  padding: 0;
}

// <1280
@include media-laptop {
  $fz-bigger: 50px;
  $fz-title: 30px;
  $fz-big: 16px;
  $fz-small-title: 20px;
  $fz-normal: 14px;
  $fz-small: 12px;
  $fz-subtitle: 28px;
  $fz-average-title: 36px;
  .fz-bigger {
    font-size: $fz-bigger;
  }
  .fz-title {
    font-size: $fz-title;
  }
  .fz-subtitle {
    font-size: $fz-subtitle;
  }
  .fz-big {
    font-size: $fz-big;
  }
  .fz-small-title {
    font-size: $fz-small-title;
  }
  .fz-normal {
    font-size: $fz-normal;
  }
  .fz-small {
    font-size: $fz-small;
  }
  .fz-average-title {
    font-size: $fz-average-title;
  }
}
// <992
@include media-tablet-horizontal {
  $fz-big: 14px;
  $fz-title: 26px;
  $fz-small-title: 18px;
  $fz-bigger: 42px;
  $fz-average: 16px;
  $fz-subtitle: 24px;
  $fz-average-title: 32px;
  .fz-average {
    font-size: $fz-average;
  }
  .fz-bigger {
    font-size: $fz-bigger;
