    font-size: $fz-big;
  }
  .fz-small-title {
    font-size: $fz-small-title;
  }
  .fz-average-title {
    font-size: $fz-average-title;
  }
}
// <768
@include media-tablet {
  $fz-title: 22px;
  $fz-bigger: 38px;
  $fz-average: 14px;
  $fz-subtitle: 20px;
  $fz-average-title: 28px;

  .fz-average-title {
    font-size: $fz-average-title;
  }
  .fz-subtitle {
    font-size: $fz-subtitle;
  }
  .fz-average {
    font-size: $fz-average;
  }
  .fz-bigger {
    font-size: $fz-bigger;
  }
  .fz-title {
    font-size: $fz-title;
  }
}
// <480
@include media-mobile {
  $fz-bigger: 34px;
  $fz-average-title: 26px;

  .fz-average-title {
    font-size: $fz-average-title;
  }
  .fz-bigger {
    font-size: $fz-bigger;
  }
}
