ul{
  margin: 0;
  padding: 0;
}

.row{
  display: flex;
  &_center{
    align-items: center;
    justify-content: center;
  }
  &_start{
    align-items: flex-start;
    justify-content: flex-start;
  }
  &_end{
    align-items: flex-end;
    justify-content: flex-end;
  }
  &_vertical-center{
    align-items: center;
  }
  &_horizontal-center{
    justify-content: center;
  }
  &_vertical-start{
    align-items: flex-start;
  }
  &_horizontal-start{
    justify-content: flex-start;
  }
  &_vertical-end{
    align-items: flex-end;
  }
  &_horizontal-end{
    justify-content: flex-end;
  }
}