.logo {
  // .logo_white
  display: flex;
  align-items: center;

  // .logo_white
  &_white {
    .logo__line {
      background-color: $text-color;
    }

    .logo__image {
      svg {
        fill: $text-color;
      }
    }

    .logo__text {
      color: $text-color;
    }
  }
  // .logo_dark
  &_dark {
    .logo__line {
      background-color: #FFF;
    }

    .logo__image {
      svg {
        fill: #FFF;
      }
    }

    .logo__text {
      color: #FFF;
    }
  }

  // .logo__image
  &__image {
    display: flex;
  }

  // .logo__line
  &__line {
    position: relative;

    width: 1px;
    height: 48px;
    margin: 0 20px;
  }

  // .logo__text
  &__text {
    font-weight: 300;

    span {
      font-weight: 350;
    }
  }
}

