.whatsapp {
  display: flex;
  flex-direction: column;
  align-items: flex-end;

  text-align: right;
  // .whatsapp_white
  &_white {
    .whatsapp__number{
      color: $text-color;
    }
    .whatsapp__text{
      color: $text-color;

      opacity: .8;
    }
  }
  // .whatsapp_dark
  &_dark {
    .whatsapp__number{
      color: #FFF;
    }
    .whatsapp__text{
      color: #FFF;

      opacity: .6;
    }
  }

  // .whatsapp__number
  &__number {
    font-weight: 350;
    line-height: 1;
    text-decoration: none;
  }
  
  // .whatsapp__text
  &__text {
    font-weight: 400;
    line-height: 1.1;
  }
}
